import constants.Constants
import kotlinx.browser.document
import kotlinx.html.dom.create
import kotlinx.html.id
import kotlinx.html.js.div
import kotlinx.html.style
import org.w3c.dom.HTMLDivElement


fun popup(text: String): HTMLDivElement {
    val d = document.create.div {
        id = "popper"
        style = """
                        position: absolute;
                        top: 10%;
                        left: 0px;
                        width: 100%;
                        animation-name: popWarning;
                        animation-delay: ${Constants.popupDelay};
                        animation-duration: ${Constants.popupRoll};
                        animation-fill-mode: forwards;
                        
                    """
    }
    d.append(
        document.create.div {
            text(text)
            style = """
                font-family: openSans, sans-serif;
                font-size: 140%;
                background-color: ${Constants.theme.backgroundColour};
                padding: 20px;
            """.trimIndent()
        }.also {
            it.className = "buttonClass"
            it.style.marginLeft = "auto"
            it.style.marginRight = "auto"
            it.style.display = "block"
            it.style.width = "50%"
        }
    )

    d.addEventListener("animationend", {
        d.remove()
    })
    return d
}


fun toPx(v: Double): String {
    return v.toInt().toString() + "px"
}

fun toPx(v: Int): String {
    return v.toString() + "px"
}