fun main() {
    println(document.cookie)
    Constants.first = accessCookie("first").toIntOrNull() == null
    val played = if (accessCookie("played").toIntOrNull() == null) 0 else accessCookie("played").toInt()
    val win = if (accessCookie("win").toIntOrNull() == null) 0 else accessCookie("win").toInt()
    val streak = if (accessCookie("streak").toIntOrNull() == null) 0 else accessCookie("streak").toInt()
    val maxStreak = if (accessCookie("maxStreak").toIntOrNull() == null) 0 else accessCookie("maxStreak").toInt()
    val record = if (accessCookie("record").toIntOrNull() == null) 0 else accessCookie("record").toInt()
    val dist = mutableListOf<Int>()
    (1..6).forEach {
        dist.add(if (accessCookie("d$it").toIntOrNull() == null) 0 else accessCookie("d$it").toInt())
    }
    val stats = Stats(played, win, streak, maxStreak, record, dist)
    val themeCommand = accessCookie("themeOption")
    Constants.theme = when (themeCommand) {
        "1" -> {
            Theme.DARK
        }
        "2" -> {
            Theme.PUNGO
        }
        else -> {
            Theme.LIGHT
        }
    }

    val nsfwCommand = accessCookie("nsfwOption")
    Constants.nsfwFilterOn = nsfwCommand != "1"


    val keyboardCommand = accessCookie("keysetOption")
    Constants.buttonSet = when (keyboardCommand) {
        "0" -> Keyset.TURKISH_Q
        "1" -> Keyset.TURKISH_F
        else -> Keyset.TURKISH_Q
    }

    val game = Game(Constants.letterLimit, Constants.attempts, stats)

    document.body?.sayHello(game = game)
    if (window.location.href.contains("seed")) {
        try {
            val seed = window.location.href.split("?")[1].split("=")[1]
            game.loadWords("toLoad/letter_5_sorted.txt", "toLoad/ngwords_5.txt", true, seed = seed.toInt())
        } catch (e: Exception) {
            game.loadWords("toLoad/letter_5_sorted.txt", "toLoad/ngwords_5.txt", true)
        }
    } else {
        game.loadWords("toLoad/letter_5_sorted.txt", "toLoad/ngwords_5.txt", true)
    }



    //      constants.Constants.dictionary.lookup("kalem")
    //      generateCookie("color", c)
    //      accessCookie("color"))


    document.onload = {
        document.body!!.style.backgroundColor = Constants.theme.backgroundColour
        Unit
    }



    window.onresize = {
        document.body!!.style.height = toPx(window.innerHeight)

        Unit
    }

}

fun updateTimer() {
    if (timerActive) {
        Constants.seconds++
        val m = Constants.seconds / 60
        val s = Constants.seconds % 60
        val mt = if (m < 10) "0$m" else m.toString()
        val st = if (s < 10) "0$s" else s.toString()
        gamePage.updateTimer("$mt:$st")
    }
}


fun generateCookies(names: List<String>, values: List<String>) {
    val cookieDate = Date()
    js("cookieDate.setFullYear(cookieDate.getFullYear() +10)")
    val ex = "expires=${cookieDate.toUTCString()}"
    names.indices.forEach {
        document.cookie = "${names[it]}=${values[it]};$ex"
    }
}


fun accessCookie(name: String): String {
    val value = document.cookie.split("; ").find { row -> row.startsWith(name) }
    return if (value != null) value.split('=')[1]
    else ""
}



fun Node.sayHello(game: Game) {

    document.addEventListener("click", { event ->
        event.preventDefault()
        event.stopPropagation()
    })

    gamePage.onChallengePressed = {
        game.challenge()
    }

    val ultra = gamePage.ultra
    val sc = SizeCalculator(ultra, game)
    val howToPage = HowToPage()
    val statisticsPage = StatisticsPage()
    val optionsPage = OptionsPage()
    val streakPage = StreakPage()


    val gridPart = GridPart(
        sc.appWidth.toDouble(),
        sc.gridHeight,
        Constants.attempts,
        Constants.letterLimit
    )

    gridPart.onGridRemove = {
        game.removeLetter(it)
        gridPart.writeLetter(game.recordedWord)
    }

    document.onkeydown = {
        gamePage.realKeyboardPressed(it.key)
    }
    gamePage.onLetterPressed = { c ->
        if (game.playable) {
            game.addLetter(c)
            gridPart.writeLetter(game.recordedWord)
        }

    }

    gamePage.onErasePressed = {
        if (game.playable) {
            game.removeLetter()
            gridPart.writeLetter(game.recordedWord)
        }
    }

    Constants.onUpdateTheme = {
        gridPart.paintRow(game.guessDatabase)
    }

    optionsPage.onNsfwFilterChange = {
        if (Constants.gameLoaded) {
            if (it) { // filter is turned on
                gamePage.onGameReset()
            } else { // filter is turned off

            }
        }

    }

    gamePage.onEnterPressed = {
        if (game.playable) {


            val res = game.checkWord()
            if (res.warningNeeded) {
                document.getElementById("main")!!.append(popup(res.warningText))
            }


            gridPart.paintRow(game.guessDatabase)
            gamePage.paintKeys(game.guessDatabase, game.letters)
            if (game.gameSolved) {
                val time = Constants.seconds
                game.updateLastGameData(game.activeRow + 1, gamePage.timerText, true)
                timerActive = false
                game.stats.played++
                game.stats.win++
                game.stats.streak++
                if (game.stats.streak > game.stats.maxStreak) game.stats.maxStreak = game.stats.streak
                if (time < game.stats.timeRecord) game.stats.timeRecord = time
                game.stats.distribution[game.activeRow]++
                val names = listOf("played", "win", "streak", "maxStreak", "record", "d1", "d2", "d3", "d4", "d5", "d6")
                val values = game.statValues
                game.stats.distribution.forEach { values.add(it.toString()) }
                generateCookies(names, values)
                gamePage.topPart.statsFunction()
                println(document.cookie)
            }
            if (!game.gameSolved && game.activeRow == 6) {
                game.updateLastGameData(-1, gamePage.timerText, false)
                Constants.seconds = 0
                timerActive = false
                gamePage.topPart.statsFunction()
                game.stats.played++
                game.stats.streak = 0
                val names = listOf("played", "streak")
                val values = mutableListOf(game.stats.played.toString(), game.stats.streak.toString())
                generateCookies(names, values)
                println(document.cookie)
            }
            gridPart.activeRow = game.activeRow
            gridPart.moveTriangle()
        }
    }

    gamePage.onGameReset = {
        game.startRound()
        game.recordedWord = ""
        gridPart.clear()
        gridPart.activeRow = game.activeRow
        gridPart.moveTriangle()
        Constants.seconds = 0
        timerActive = true
    }

    optionsPage.isPhone = js("(/(android|bb\\d+|meego).+mobile|avantgo|bada\\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|mobile.+firefox|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\\.(browser|link)|vodafone|wap|windows ce|xda|xiino/i.test(navigator.userAgent || navigator.vendor || window.opera) || /1207|6310|6590|3gso|4thp|50[1-6]i|770s|802s|a wa|abac|ac(er|oo|s\\-)|ai(ko|rn)|al(av|ca|co)|amoi|an(ex|ny|yw)|aptu|ar(ch|go)|as(te|us)|attw|au(di|\\-m|r |s )|avan|be(ck|ll|nq)|bi(lb|rd)|bl(ac|az)|br(e|v)w|bumb|bw\\-(n|u)|c55\\/|capi|ccwa|cdm\\-|cell|chtm|cldc|cmd\\-|co(mp|nd)|craw|da(it|ll|ng)|dbte|dc\\-s|devi|dica|dmob|do(c|p)o|ds(12|\\-d)|el(49|ai)|em(l2|ul)|er(ic|k0)|esl8|ez([4-7]0|os|wa|ze)|fetc|fly(\\-|_)|g1 u|g560|gene|gf\\-5|g\\-mo|go(\\.w|od)|gr(ad|un)|haie|hcit|hd\\-(m|p|t)|hei\\-|hi(pt|ta)|hp( i|ip)|hs\\-c|ht(c(\\-| |_|a|g|p|s|t)|tp)|hu(aw|tc)|i\\-(20|go|ma)|i230|iac( |\\-|\\/)|ibro|idea|ig01|ikom|im1k|inno|ipaq|iris|ja(t|v)a|jbro|jemu|jigs|kddi|keji|kgt( |\\/)|klon|kpt |kwc\\-|kyo(c|k)|le(no|xi)|lg( g|\\/(k|l|u)|50|54|\\-[a-w])|libw|lynx|m1\\-w|m3ga|m50\\/|ma(te|ui|xo)|mc(01|21|ca)|m\\-cr|me(rc|ri)|mi(o8|oa|ts)|mmef|mo(01|02|bi|de|do|t(\\-| |o|v)|zz)|mt(50|p1|v )|mwbp|mywa|n10[0-2]|n20[2-3]|n30(0|2)|n50(0|2|5)|n7(0(0|1)|10)|ne((c|m)\\-|on|tf|wf|wg|wt)|nok(6|i)|nzph|o2im|op(ti|wv)|oran|owg1|p800|pan(a|d|t)|pdxg|pg(13|\\-([1-8]|c))|phil|pire|pl(ay|uc)|pn\\-2|po(ck|rt|se)|prox|psio|pt\\-g|qa\\-a|qc(07|12|21|32|60|\\-[2-7]|i\\-)|qtek|r380|r600|raks|rim9|ro(ve|zo)|s55\\/|sa(ge|ma|mm|ms|ny|va)|sc(01|h\\-|oo|p\\-)|sdk\\/|se(c(\\-|0|1)|47|mc|nd|ri)|sgh\\-|shar|sie(\\-|m)|sk\\-0|sl(45|id)|sm(al|ar|b3|it|t5)|so(ft|ny)|sp(01|h\\-|v\\-|v )|sy(01|mb)|t2(18|50)|t6(00|10|18)|ta(gt|lk)|tcl\\-|tdg\\-|tel(i|m)|tim\\-|t\\-mo|to(pl|sh)|ts(70|m\\-|m3|m5)|tx\\-9|up(\\.b|g1|si)|utst|v400|v750|veri|vi(rg|te)|vk(40|5[0-3]|\\-v)|vm40|voda|vulc|vx(52|53|60|61|70|80|81|83|85|98)|w3c(\\-| )|webc|whit|wi(g |nc|nw)|wmlb|wonu|x700|yas\\-|your|zeto|zte\\-/i.test((navigator.userAgent || navigator.vendor || window.opera).substr(0, 4)))") as Boolean

    val gameDiv = gamePage.generate(gridPart, sc)
    val howToDiv = howToPage.generate(sc.appWidth.toDouble(), gamePage.topPart.heightToWidth)
    val statsDiv = statisticsPage.generate(sc.appWidth.toDouble(), gamePage.topPart.heightToWidth)
    val streakDiv = streakPage.generate(sc.appWidth.toDouble(), gamePage.topPart.heightToWidth)
    val optionsDiv = optionsPage.generate(sc.appWidth.toDouble(), gamePage.topPart.heightToWidth)
    howToDiv.hidden = true
