    }

    gamePage.topPart.statsFunction = {
        gameDiv.hidden = true

        if(game.stats.streak in listOf(5,10,25,50,100,250,500,1000,2500,5000,10000) && game.lastGameData.success){
            streakPage.updateStreakInfo(game.stats.streak)
            streakDiv.hidden=false
        }else{
            streakPage.updateStreakInfo(game.stats.streak)
            statsDiv.hidden=false
        }
        Constants.dictionary.lookup(game.gameWord) {
            try {
                statisticsPage.updateStats(game, it)
            } catch (e: Exception) {
