        optionsDiv.hidden = false
    }

    howToPage.returnButtonFunction = {
        howToDiv.hidden = true
        gameDiv.hidden = false
    }

    statisticsPage.returnButtonFunction = {
        statsDiv.hidden = true
        gameDiv.hidden = false
    }

    fun shareStats(streak: Boolean = false) {
       val text = if(streak) { "Szck'te son ${game.stats.streak} oyunumun hepsini kazandm. Var m benden iyisi?\n" + game.stats.generateMessage()
        } else { "Szck'te ${game.stats.played} oyunumdan ${game.stats.win} tanesini kazandm. Var m benden iyisi?\n" + game.stats.generateMessage()
        }
        val link = window.location.href
        try{
            (if(optionsPage.isPhone) {

                if (js("if(navigator.share){return true}") as Boolean) {
                    js("navigator.share({text: text, url: link}).catch(console.error);")
                } else {
                    window.navigator.clipboard.writeText((text + link))
                    document.getElementById("main")!!.append(popup("Kopyaland! Paylamaya Hazr!"))
                }
            } else {
                window.navigator.clipboard.writeText((text + link))
                document.getElementById("main")!!.append(popup("Kopyaland! Paylamaya Hazr!"))
            }) as Unit
        }catch (e: Exception){
            window.navigator.clipboard.writeText((text + link))
            document.getElementById("main")!!.append(popup("Kopyaland! Paylamaya Hazr!"))
        }
    }

    statisticsPage.shareStatisticsFunction = {
