package widgets

import SizeCalculator
import game.Guess
import kotlinx.browser.document
import kotlinx.browser.window
import kotlinx.html.dom.append
import kotlinx.html.dom.create
import kotlinx.html.id
import kotlinx.html.js.div
import kotlinx.html.style
import org.w3c.dom.HTMLDivElement
import toPx


class GamePage(){
    val topPart = TopPart().also {
        it.replayFunction = {
            resetPlayfield()
            js("document.activeElement.blur()") as Unit
        }
    }
    private val keyboard = Keyboard().also {
        //it.enterPressed = {enterPressed()} //onErasePressed
        it.erasePressed = {erasePressed()}
        it.letterPressed = {c: Char -> letterPressed(c)}
    }
    private val midBanner = MidBanner().also {
        it.challenge = {challengePressed()}
        it.onTryPressed = {enterPressed()}
    }
    private val adBanner = AdBanner()

    val ultra: Double
        get() {
            return topPart.heightToWidth+keyboard.heightToWidth+midBanner.heightToWidth+adBanner.heightToWidth
        }

    val timerText: String
        get() {
            return midBanner.timerText
        }



    var onChallengePressed={}

    var onErasePressed = {}
    var onEnterPressed = {}
    var onLetterPressed = { _: Char->}
    var sizeCalculatorUsed: SizeCalculator?= null



    fun challengePressed(){
        onChallengePressed()
    }

    private fun erasePressed(){
        onErasePressed()
    }

    private fun enterPressed(){
        onEnterPressed()
    }

    private fun letterPressed(c: Char){
        onLetterPressed(c)
    }


    fun resetKeyboard(){
        if(sizeCalculatorUsed!=null){
            val d = document.getElementById("keyboardBox")
            if(d!=null){
                d.append(keyboard.generate(sizeCalculatorUsed!!.appWidth))
            }
        }

    }

    fun paintKeys(guessDatabase: MutableList<Guess>, letters: Int) {
        keyboard.paintKeys(guessDatabase, letters)
    }


    fun realKeyboardPressed(key: String){
        if (key == "Enter") {
            midBanner.onTryPressed()
        } else if (key == "Backspace") {
            keyboard.erasePressed()
        } else if ("qwertyuopasdfghjklizxcvbnm".contains(key)) {
            keyboard.letterPressed(key.toCharArray()[0])
        }
    }

    var onGameReset = {}

    fun resetPlayfield(){
        keyboard.resetKeys()
        midBanner.updateTimer("00:00")
        onGameReset()
    }

    fun generate(gridPart: GridPart, sc: SizeCalculator): HTMLDivElement {
        sizeCalculatorUsed = sc
        return document.create.div {
            id = "gameDiv"
        }.also {
            it.append {
                div {
                    id = "topPart"
                }.also {
                    it.append(topPart.generate(sc.appWidth))
                }



                div {
                    id = "grid"
                }.also {
                    it.append(gridPart.createGrid())
                }


                div {
                    id = "commercial"
                    style = """
                        margin: auto;                        
                        position: absolute;
                        bottom: ${toPx(keyboard.expectedHeight(sc.appWidth) + midBanner.expectedHeight(sc.appWidth))};
                        left: 0;
                        background-color: #44FF44;
                    """
                }.also {
                    it.append(adBanner.generate(sc.appWidth))
                }


                div {
                    id = "midBannerBox"
                    style = """
                        width: 100%;
                        position: absolute;
                        bottom: ${toPx(keyboard.expectedHeight(sc.appWidth))};
                        left: 0px;
                    """
                }.also {
                    it.append(midBanner.generate(sc.appWidth))
                }




                div {
                    id = "keyboardBox"
                    style = """
                        width: 100%;
                        position: absolute;
                        bottom: 0px;
                        left: 0px;                        
                    """
                }.also {
                    it.append(keyboard.generate(sc.appWidth))
                }
            }
        }



    }


    fun updateTimer(s: String){
        midBanner.updateTimer(s)
    }


}