package widgets

import game.Game
import kotlinx.browser.document
import kotlinx.html.button
import kotlinx.html.dom.append
import kotlinx.html.dom.create
import kotlinx.html.id
import kotlinx.html.js.div
import kotlinx.html.js.onClickFunction
import kotlinx.html.style
import org.w3c.dom.HTMLDivElement
import toPx

class MidBanner() {
    lateinit var challenge: () -> Unit
    val heightToWidth = 72.0/600.0 // height/width
    var timerText = ""
    var onTryPressed = {}

    fun expectedHeight(width: Int): Int {
        return (width*heightToWidth).toInt()
    }

    fun generate(width: Int): HTMLDivElement {
        val height = expectedHeight(width)
        val topPart = document.create.div{
            style="""
                height: ${toPx(height)};
                width: ${toPx(width)};
            """
        }.also {
            it.id ="midbanner"
            it.style.apply {
                this.height = toPx(height)
                this.width = toPx(width)
                display="flex"
                justifyContent="center"
                alignItems = "center"

            }
        }


        val tryButton = document.create.button{
            text("Dene")
            onClickFunction = {
                onTryPressed()
            }
            style="""
                width:  ${toPx(width * 0.35)};
                height: ${toPx(height * 0.8)};
                margin: ${toPx(height * 0.1)}  ${toPx(height * 0.1)} ${toPx(height * 0.1)} ${toPx(width * 0.05)};
                font-family: openSans, sans-serif;
                font-size: ${toPx(height * 0.4)};
                font-stretch: condensed;
            """.trimIndent()
        }.also {
            it.className = "buttonClass"
        }

        val challengeButton = document.create.button{
            text("Meydan Oku")
            onClickFunction = {
                challenge()
            }
            style="""
                width:  ${toPx(width * 0.35)};
                height: ${toPx(height * 0.8)};
                margin: ${toPx(height * 0.1)} ${toPx(width * 0.05)} ${toPx(height * 0.1)}  ${toPx(height * 0.1)};
                font-family: openSans, sans-serif;
                font-size: ${toPx(height * 0.4)};
                font-stretch: condensed;
            """.trimIndent()
        }.also {
            it.className = "buttonClass"

        }

        val timer = document.create.div{
            id="timer"
            text("00:00")
            style =
                """
                        height: ${toPx(height)};
                        width: ${toPx(width*0.25)};
                        text-align: center;
                        opacity: 1;
                        float: left;
                        font-family: guessFont, sans-serif;
                        font-size: ${toPx(height * 0.6)};
                    """
        }

        topPart.append(challengeButton)
        topPart.append(timer)
        topPart.append(tryButton)

        //topPart.append(buttonHole)
        return topPart
    }

    fun updateTimer(s: String){
        timerText = s
        document.getElementById("timer")!!.textContent=s
    }

}

