data class Stats(
    var played: Int = 0,  // oynanan toplam oyun
    var win: Int = 0,  // kazanlan toplam oyun
    var streak: Int = 0, // u anki seri
    var maxStreak: Int = 0, // en uzun seri
    var timeRecord: Int = 0,
    var distribution: MutableList<Int> = MutableList(6) { 0 }
){
    val statValuesList: MutableList<String>
        get() {
            return mutableListOf(
                played.toString(),
                win.toString(),
                streak.toString(),
                maxStreak.toString(),
                timeRecord.toString()
            )
        }

    fun generateMessage() : String {
        return "Oynanan Oyun Says ........... $played \n" +
               "Kazanlan Oyun Says .......... $win \n" +
               "Galibiyet Serisi ......................... $streak \n" +
               "En Uzun Seri ............................. $maxStreak \n" +
               "Doru Tahmin Oran ............ ${"%"+(win.toDouble()/played*100.0).toInt()} \n"
    }
}