package widgets

import kotlinx.browser.document
import kotlinx.browser.window
import kotlinx.html.button
import kotlinx.html.dom.append
import kotlinx.html.dom.create
import kotlinx.html.id
import kotlinx.html.js.div
import kotlinx.html.style
import org.w3c.dom.HTMLDivElement
import toPx

class StreakPage() {
    var onContinuePressed = {

    }

    var onSharePressed = {

    }

    fun generate(width: Double, topHeightToWidth: Double): HTMLDivElement {
        val topPartHeight = width * topHeightToWidth
        return document.create.div {
            id = "someeethiiing"
            style = """
                position: absolute;
                top: 0px;
                left: 0px;
                width: ${toPx(width)};
            """.trimIndent()
        }.also {
            it.append.div {
                id = "streaktitleeeee"

                style = """
                    font-family: guessFont, sans-serif;
                    font-size: ${toPx(topPartHeight * 0.7)};
                    height: ${toPx(topPartHeight * 1)};
                    width: ${toPx(width)};
                    text-align: center;
                    margin-top: ${toPx(topPartHeight * 3)};
                    margin-bottom: ${toPx(topPartHeight * 0.2)};
                """
            }.also {
                it.textContent = "HARKA!"
            }



            it.append.div {
                id = "streaktextdiv"
                style = """
                    font-family: openSans, sans-serif;
                    font-size: ${toPx(topPartHeight * 0.3)};
                    width: 90%
                    margin-left: 5%;
                    margin-right: 5%;
                    text-align: center;
                    margin-top: ${toPx(topPartHeight * 0.3)};
                    margin-bottom: ${toPx(topPartHeight * 0.2)};
                """

            }.also {
                it.innerHTML =
                    "<p>5 Kez arka arkaya <span style=\"font-family:titleFont\">Szck</span>' doru tahmin ettin. Bunu arkadalarnla da paylamak ister misin?</p>"
            }

            it.append.div {
                style = """
                font-family: openSans, sans-serif;
                font-size: ${toPx(topPartHeight * 0.3)};
                width: 90%
                text-align: center;
                margin-top: ${toPx(topPartHeight * 0.5)};
                margin-bottom: ${toPx(topPartHeight * 0.2)};
                """.trimIndent()
            }.also {

                it.append.div { // choice row
                    style = """
                        display: flex;
                        margin: 0% 2%;
                    """.trimIndent()
                }.also { elem ->
                    document.create.button {
                        text("Payla")
                        style = """
                                flex-grow: 1;
                                height: ${toPx(topPartHeight * 0.6)};
                                font-family: openSans, sans-serif;
                                font-size: ${toPx(topPartHeight * 0.3)};
                                margin-left: 3%;
                            """
                    }.also {
                        it.className = "buttonClass"
                        it.onclick = {
                            shareButton()
                        }
                        elem.append(it)
                    }

                    document.create.button {
                        text("Devam et")
                        style = """
                                flex-grow: 1;
                                height: ${toPx(topPartHeight * 0.6)};
                                font-family: openSans, sans-serif;
                                font-size: ${toPx(topPartHeight * 0.3)};
                                margin-left: 3%;
                            """
                    }.also {
                        it.className = "buttonClass"
                        it.onclick = {
                            continueButton()

                        }
                        elem.append(it)
                    }



                }


            }




        }


    }


    fun continueButton(){
        onContinuePressed()
    }

    fun shareButton(){
        onSharePressed()

    }

    fun updateStreakInfo(streak: Int) {
        document.getElementById("streaktextdiv")!!.innerHTML = "<p>$streak Kez arka arkaya <span style=\"font-family:titleFont\">Szck</span>' doru tahmin ettin. Bunu arkadalarnla da paylamak ister misin?</p>"
    }
}