package widgets

import accessCookie
import constants.Constants
import constants.Keyset
import constants.Theme
import generateCookies
import kotlinx.browser.document
import kotlinx.browser.window
import kotlinx.html.button
import kotlinx.html.dom.append
import kotlinx.html.dom.create
import kotlinx.html.id
import kotlinx.html.js.a
import kotlinx.html.js.div
import kotlinx.html.js.onClickFunction
import kotlinx.html.style
import org.w3c.dom.HTMLDivElement
import org.w3c.dom.HTMLElement
import org.w3c.dom.asList
import org.w3c.dom.events.Event
import toPx

class OptionsPage {

    var returnButtonFunction = {}
    var optionsDict = mutableMapOf<String,Option>() // use this to access a particular option
    private set

    var onNsfwFilterChange = {b: Boolean ->}
    var makeIconButtonEvent = {}
    var isPhone = false

    fun generate(width: Double, topHeightToWidth: Double): HTMLDivElement {
        val topPartHeight = width*topHeightToWidth
        optionsDict["theme"] = Option("themeOption","Ik Temas",listOf("PunGo","Aydnlk","Karanlk"),topPartHeight){i->changeTheme(i)}
        optionsDict["diff"] = Option("diffOption","Zor Szckler (Tercih sonraki szcklerde etkildir.)",listOf("Gelmesin","Gelsin"),topPartHeight){i->changeDifficulty(i)}
        optionsDict["keyset"] = Option("keysetOption","Tu Dizilimi",listOf("Q Klavye","F Klavye"),topPartHeight){i->changeKeyset(i)}
        optionsDict["nsfw"] = Option("nsfwOption","Aypl Szckler (Dikkat! Ayar kapatmak oyunu yeniler.)",listOf("Kapal","Ak"),topPartHeight){i->changeNSFW(i)}


        return document.create.div {
            id ="howToDiv"

        }.also {
            it.append(generateTopPart(topPartHeight))
            optionsDict.forEach { mapEntry->
                it.append(mapEntry.value.element)
            }


            it.append.div {
                style =
                    """
                        font-family: openSans, sans-serif;
                        font-size: ${toPx(topPartHeight * 0.3)};
                        width: 90%
                        text-align: left;
                        margin-top: ${toPx(topPartHeight * 0.5)};
                        margin-bottom: ${toPx(topPartHeight * 0.2)};
                        margin-left: 5%;
                    """
                text("Bize ulamak iin:")
            }.also {
                it.append.div {

                }.also {
                    it.style.apply{
                        display="flex"
                    }

                    it.append.div {
                        style = """
                        user-select: none;
                        color: #FF7B00;
                        text-decoration: underline;
                        cursor: grab;
                        margin-top: 5%;
                    """
                        text("twitter")
                    }.also {
                        it.onclick = {
                            window.open("https://twitter.com/PunGoCorp")
                        }
                    }

                    it.append.div {
                        style = """
                        user-select: none;
                        color: #FF7B00;
                        text-decoration: underline;
                        cursor: grab;
                        margin-top: 5%;
                        margin-left: 10px;
                    """
                        text("instagram")
                    }.also {
                        it.onclick = {
                            window.open("https://www.instagram.com/pungo_corp/")
                        }
                    }

                    it.append.div {
                        style = """
                        user-select: none;
                        color: #FF7B00;
                        text-decoration: underline;
                        cursor: grab;
                        margin-left: 10px;
                        margin-top: 5%;
                    """
                        text("websitemiz")
                    }.also {
                        it.onclick = {
                            window.open("pungo.games")
                        }
                    }
                }




                it.append.div {
                    style = """
                        color: #FF7B00;
                        margin-top: 5%;
                    """
                    text("pungocorp@gmail.com")
                }.also {
                }

                it.append.div {
                    style = """
                        position: absolute;
                        bottom: 0px;
                        left: 0px;
                        margin-left: 5%;
                        margin-right: 5%;  
                        margin-bottom: 5%;
                        width: 90%;
                    """
                }.also {
                    it.append.button {
                        style = """
                            height: ${toPx(topPartHeight * 0.6)};
                            font-family: openSans, sans-serif;
                            font-size: ${toPx(topPartHeight * 0.3)};
                            width: 100%;
                        """.trimIndent()
                        text("Telefon Ekranna Ekle")
                    }.also {
                        it.className = "buttonClass"
                        it.onclick = {
                            makeIconButtonEvent()
                        }
                        if(!isPhone){
                            it.hidden = true
                        }

                    }
                }


            }

        }
    }


    fun changeDifficulty(i: Int){
        Constants.difficultyFiltered = i==0
    }



    fun changeKeyset(i: Int){
        Constants.updateKeyset(
            when(i){
                0-> Keyset.TURKISH_Q
                1-> Keyset.TURKISH_F
                else -> Keyset.TURKISH_Q
            }
        )
    }

    fun changeTheme(i: Int){
        document.getElementsByClassName("buttonClass").asList().forEach {
            (it as HTMLElement).style.backgroundColor =  Constants.theme.buttonColour
        }
        Constants.updateTheme(
            when(i){
                0-> Theme.PUNGO
                1-> Theme.LIGHT
                2-> Theme.DARK
                else -> Theme.PUNGO
            }
        )
    }

    fun changeNSFW(i: Int){
        Constants.nsfwFilterOn = when(i){
            0->true
            1-> false
            else-> true
        }
        onNsfwFilterChange(Constants.nsfwFilterOn)
    }

    fun setOptions(themeOption: Int, nsfwOption: Int){
        optionsDict["theme"]!!.switchTo(themeOption)
        optionsDict["nsfw"]!!.switchTo(nsfwOption)
    }






    class Option(val elementId: String, val title: String, val options: List<String>, topPartHeight: Double, var picked: Int = -1, var onSwitch: (number: Int)->Unit = {}){
        val element: HTMLDivElement
        private val optionButtons = mutableListOf<HTMLElement>()
        init {
            picked = if(picked==-1){
                val s = accessCookie(elementId)
                if(s==""){
                    0
                }else{
                    s.toInt()
                }
            }else{
                picked
            }



            element = document.create.div {
                id=elementId
                style = """
                font-family: openSans, sans-serif;
                font-size: ${toPx(topPartHeight * 0.3)};
                width: 90%
                text-align: center;
                margin-top: ${toPx(topPartHeight * 0.5)};
                margin-bottom: ${toPx(topPartHeight * 0.2)};
                """.trimIndent()
            }.also {

                it.append.div { // text
                    id = "${elementId}_text"
                    style="""
                        margin: 2% 5%;
                        width: 90%;
                        text-align: left;
                    """
                    text(title)
                }

                it.append.div { // choice row
                    style="""
                        display: flex;
                        margin: 0% 2%;
                    """.trimIndent()

                }.also {elem->
                    options.forEachIndexed() { index, s ->
                        document.create.button {
                            id = elementId +"_op_"+index.toString()
                            text(s)
                            style = """
                                flex-grow: 1;
                                height: ${toPx(topPartHeight * 0.6)};
                                font-family: openSans, sans-serif;
                                font-size: ${toPx(topPartHeight * 0.3)};
                                margin-left: 3%;
                            """
                        }.also {
                            it.className = "buttonClass"
                            it.onclick = {
                                switchTo(index)
                            }
                            elem.append(it)
                            optionButtons.add(it)
                        }
                    }

                }
                switchTo(picked)

            }

        }

        fun switchTo(item: Int){
            picked=item

            generateCookies(listOf(this.elementId), listOf(this.picked.toString()))
            println(document.cookie)
            onSwitch(item)
            optionButtons.forEachIndexed { index, htmlElement ->
                if(index==item){
                    //window.alert(htmlElement.style.backgroundColor)
                    htmlElement.style.backgroundColor = Constants.theme.greenColour
                    htmlElement.style.setProperty("--state","green")
                }else{
                    //window.alert(htmlElement.style.backgroundColor)
                    htmlElement.style.backgroundColor = Constants.theme.buttonColour
                    htmlElement.style.setProperty("--state","0")
                }
            }
        }


    }



    fun generateTopPart(topPartHeight: Double): HTMLDivElement {
        return document.create.div{ // top row
            id= "settingsTopRow"
            style ="""
                            width: 100%;
                            height: ${toPx(topPartHeight)};                     
                        """
        }.also {
            it.append {
                div{
                    id="titleDiv"
                    style =
                        """
                                        height:  ${toPx(topPartHeight)};
                                        text-align: left;
                                        margin-left: 5%;
                                        float: left;
                                        font-family: guessFont, sans-serif;
                                        font-size: ${toPx(topPartHeight*0.7)};
                                        text-align: center;
                                        line-height: ${toPx(topPartHeight)};
                                    """

                    text("Ayarlar")
                }

                div{
                    id="titleDiv"
                    style =
                        """
                                float: right;
                                height:  ${toPx(topPartHeight)};
                                width:  ${toPx(topPartHeight)};
                                text-align: center;
                                margin-top: auto;
                                """
                }.also {
                    it.append{
                        val extra = if(document.URL.contains("localhost")){
                            ""
                        }else{
                            "/sozck"
                        }

                        button{
                            onClickFunction = {
                                returnButtonFunction()
                            }
                            style = """
                                        opacity: 1;
                                        width:  ${toPx(topPartHeight * 0.8)};
                                        border: none;
                                        height: ${toPx(topPartHeight * 0.8)};
                                        margin-top: ${toPx(topPartHeight * 0.1)};
                                        background: transparent;
                                        background-image: url("${extra}/icon/cross.png");
                                        background-repeat: no-repeat;
                                        background-size: ${toPx(topPartHeight * 0.5)} ${toPx(topPartHeight * 0.5)};
                                        background-position: center;
                                    """
                        }.also {
                            it.className = "iconClass"
                        }
                    }
                }
            }
        }
    }



}