package game

import constants.Constants
import Stats
import kotlinx.browser.document
import kotlinx.browser.window
import popup
import kotlin.random.Random

class Game(var letters: Int, val attempts: Int, val stats: Stats) {
    var recordedWord = ""
    var activeRow = 0
    val wordList = mutableListOf<Word>()
    var activeWordList = listOf<String>()
    var nsfwWordList = mutableListOf<String>()
    var gameWord = ""
    var gameSolved = false
    var gameLost = false
    var gameSeed = -1
    val playable: Boolean
    get() {
        return !gameSolved && !gameLost
    }
    var guessDatabase = mutableListOf<Guess>()
    val gridRate: Double
    get() {
        return letters.toDouble()/attempts.toDouble()
    }
    var lastGameData = GameData("",0,"", false)
    val statValues: MutableList<String>
    get() {
        return stats.statValuesList
    }



    fun loadWords(filepath: String, otherFilePath: String,  andStart: Boolean = false, seed: Int = -1) {
        val url = window.location.href.split("?")[0]

        var firstFlag = false
        var secondFlag = false

        Constants.dictionary.getAsync(url + filepath) { list ->

            wordList.addAll(list.lines().map{ it.split("_").let {
                if((it[1]!="z").and(it[1]!="k")) {
                    window.alert(it[0])
                }
                Word(it[0],it[1])}
            })


            if (andStart && secondFlag) {
                startRound(seed = seed)
            }else{
                firstFlag = true
            }
        }

        Constants.dictionary.getAsync(url + otherFilePath) { list ->
            nsfwWordList.addAll(list.lines())
            if (andStart && firstFlag) {
                startRound(seed = seed)
            }else{
                secondFlag = true
            }
        }

    }



    fun startRound(letters: Int = this.letters, seed: Int = -1) {

        activeRow = 0
        guessDatabase.clear()
        gameSolved = false
        gameLost = false
        if (letters!=this.letters){
            this.letters = letters
        }
        activeWordList = wordList.map { it.word }
        gameWord = selectWord(seed)

    }

    fun selectWord(seed: Int=-1): String {
        gameSeed = if(seed==-1){
            (1..1000000).random()
        }else{
            seed
        }
        val w = wordList.random(Random(gameSeed))

        if(w.diff=="z" && Constants.difficultyFiltered && seed==-1){
            return selectWord()
        }

        return if(Constants.nsfwFilterOn){
            if(w.word in nsfwWordList){
                if(seed!=-1){
                    window.alert("TESTING")
                    popup("Bu meydan okuma ayp bir kelime. Ayp filtresi kapalyken kabul edilemez.")
                    window.alert("Bu meydan okuma ayp bir kelime. Ayp filtresi kapalyken kabul edilemez.")
                }
                selectWord()
            }else{
                w.word
            }
        }else{
            w.word
        }
    }

    fun createChallenge(): String {
        return window.location.href.split("?")[0] + "?seed=" + gameSeed
    }

    fun addLetter(c: Char) {
        if(recordedWord.contains('_')){
            recordedWord = recordedWord.replaceFirst('_',c)
        }else if (recordedWord.length < letters) {
                recordedWord += c
        }else{
            // too long
        }




    }

    fun removeLetter(index: Int=-1) {
        recordedWord = if((index>=recordedWord.length) or (index<0)){
            recordedWord.dropLast(1)
        }else{
            recordedWord.toCharArray().mapIndexed { ind, c -> if(ind != index) c else '_' }.joinToString("")
        }
        if(recordedWord.lastOrNull()=='_'){
            removeLetter()
        }

    }

    fun checkWord(): CheckReturn {
        if(activeWordList.isEmpty()){
            window.alert("kelime yklenmedi")
            return CheckReturn.ERROR
        }

        if (recordedWord.length != letters) { // not enough letters
            return CheckReturn.LESS_LETTERS
        }

        if(recordedWord=="pungo"){
            return CheckReturn.PUNGO
        }

        if(recordedWord !in activeWordList){ // not a word
            return CheckReturn.UNLISTED_WORD
        }

        if((recordedWord in nsfwWordList)&& Constants.nsfwFilterOn){ // aypl kelime
            return CheckReturn.NSFW
        }



        // rock & roll
        val newGuesses = (0 until letters).map {i-> Guess(recordedWord[i],i,-1,activeRow)}
        val gamePairs = (0 until letters).map { i-> Pair(i,gameWord[i]) }.toMutableList()


       // var miniRecord = gameWord.toMutableList()
        var miniIndex = 0
        newGuesses.forEachIndexed {index, it->
            if(gameWord[it.slot]==it.letter){
                newGuesses[index].result = GuessResult.RIGHT_PLACE
                newGuesses[index].matchSlot = gamePairs[miniIndex].first
                gamePairs.removeAt(miniIndex)
            }else{
                miniIndex += 1
            }
        }

        if(gamePairs.isEmpty()){
            gameSolved = true
            guessDatabase.addAll(newGuesses)
            return CheckReturn.SOLVED
        }

        newGuesses.forEachIndexed{index, it->
            if(it.result!=GuessResult.RIGHT_PLACE){
                val ind = gamePairs.indexOfFirst {gp-> gp.second==it.letter  }
                if(ind!=-1){
                    newGuesses[index].result = GuessResult.WRONG_PLACE
                    newGuesses[index].matchSlot = gamePairs[ind].first
                    gamePairs.removeAt(ind)
                }else{
                    newGuesses[index].result = GuessResult.WRONG
                }
            }
        }

        guessDatabase.addAll(newGuesses)
        if(activeRow<attempts){
            recordedWord = ""
            activeRow+=1
        }else{

            gameLost = true
        }
        return CheckReturn.UNSOLVED
    }

    fun updateLastGameData(activeRow: Int, time: String, success: Boolean){
        lastGameData = GameData(gameWord, activeRow, time, success)
    }

    fun challenge() {
        val link = createChallenge()
        val text: String
        if (gameSolved) {
            println("SUCCESS")
            text = "Ben ${activeRow + 1} tahminde szc buldum, bakalm sen ka tahminde bulacaksn?\n"
        } else if (activeRow == 6) {
            println("FAILURE")
            text = "Ben baaramadm, bakalm sen szc bulabilecek misin?\n"
        } else {
            println("YET TO TRY")
            text = "Bu szck iin sana meydan okuyorum!\n"
        }


        try{
            if (js("if(navigator.share){return true}") as Boolean) {
                js("navigator.share({text: text, url: link}).catch(console.error);")
            } else {
                window.navigator.clipboard.writeText((text + link))
                document.getElementById("main")!!.append(popup("Link kopyaland! Paylamaya Hazr!"))
            }
        }catch (e: Exception){
            window.navigator.clipboard.writeText((text + link))
            document.getElementById("main")!!.append(popup("Link kopyaland! Paylamaya Hazr!"))
        }
    }


    data class Word(val word: String, val diff: String)


    data class GameData(val word: String, val attempts: Int, val time: String, val success: Boolean){
        val isValid: Boolean get() {return word!=""}

        val wordUppercase: String
            get(){
                return word.toList().map { turkishUppercase(it) }.joinToString("")
            }

        private fun turkishUppercase(c: Char): Char {
            return if(c==''){
                'I'
            }else if(c=='i'){
                ''
            }else{
                c.uppercaseChar()
            }
        }

    }



    enum class CheckReturn{
        ERROR {
            override val warningNeeded: Boolean
                get() = false
            override val warningText: String
                get() = ""
        },
        LESS_LETTERS {
            override val warningNeeded: Boolean
                get() = true
            override val warningText: String
                get() = "Yetersiz harf"
        },
        PUNGO {
            override val warningNeeded: Boolean
                get() = true
            override val warningText: String
                get() = "Biz de sizi ok seviyoruz."
        },
        UNLISTED_WORD {
            override val warningNeeded: Boolean
                get() = true
            override val warningText: String
                get() = listOf("Byle szck olmaz olsun.",
                    "Bu szc uydurduunu hepimiz biliyoruz!",
                    "Biz de TDK'nn yalancsyz.",
                    "Bunun szck olduuna gerekten emin misin?",
                    "Szck de be harfli ama onu da yazamazsn!",
                    "TDKya yazyoruz yaknda szle eklerler.").random()
        },
        SOLVED {
            override val warningNeeded: Boolean
                get() = false
            override val warningText: String
                get() = "Cevap bulundu, yr be."
        },
        UNSOLVED {
            override val warningNeeded: Boolean
                get() = false
            override val warningText: String
                get() = ""
        },
        NSFW {
            override val warningNeeded: Boolean
                get() = true
            override val warningText: String
                get() = "Bu kelime aypl. Aypl kelimeler kapalyken kullanlamaz."
        };

        abstract val warningNeeded: Boolean
        abstract val warningText: String



    }
}