package game

import constants.Constants

data class Guess(val letter: Char, val slot: Int, var matchSlot: Int, val attempt: Int, var result: GuessResult = GuessResult.UNDECIDED)

enum class GuessResult{
    UNDECIDED {
        override fun colour(): String {
            return "#000000"
        }
    },
    RIGHT_PLACE {
        override fun colour(): String {
            return Constants.theme.greenColour
        }
    },
    WRONG_PLACE {
        override fun colour(): String {
            return Constants.theme.yellowColour
        }
    },
    WRONG {
        override fun colour(): String {
            return Constants.theme.grayColour
        }
    };

    abstract fun colour(): String
}


