import kotlinx.serialization.decodeFromString
import kotlinx.serialization.json.Json
import org.w3c.xhr.XMLHttpRequest

class Dictionary {
    private val dictURL = "https://sozluk.gov.tr/gts?ara="
    var anlam = ""

    fun getAsync(url: String, timeout: Int = 10000, callback: (String) -> Unit) {
        val xmlHttp = XMLHttpRequest()
        xmlHttp.open("GET", url)
        xmlHttp.timeout = timeout
        println(xmlHttp.timeout)
        xmlHttp.onload = {
            println("GETTING MEANING")
            if (xmlHttp.readyState == 4.toShort() && xmlHttp.status == 200.toShort()) {
                println("FOUND MEANING")
                callback.invoke(xmlHttp.responseText)
            } else {
                callback.invoke("")
            }
        }
        xmlHttp.ontimeout = {
            println("TIMEOUT")
            callback.invoke("")
        }
        xmlHttp.onerror = {
            callback.invoke("")
        }
        xmlHttp.send()
    }

    fun lookup(word: String, callback: (String) -> Unit) {
        val url = dictURL + word
        getAsync(url,1000) {
            if(it != "") {
                val json = Json { ignoreUnknownKeys = true }
                val res = json.decodeFromString<List<Anlam.AnlamItem>>(it)[0].anlamlarListe[0].anlam
                callback.invoke(res)
            } else {
                callback.invoke("")
            }
        }
    }
}