import kotlinx.serialization.SerialName
import kotlinx.serialization.Serializable

@Serializable
class Anlam : ArrayList<Anlam.AnlamItem>(){
    @Serializable
    data class AnlamItem(
        @SerialName("anlamlarListe")
        val anlamlarListe: List<AnlamlarListe>,
    ) {
        @Serializable
        data class AnlamlarListe(
            @SerialName("anlam")
            val anlam: String
        )
    }
}